
<script>
import ShareDataService from "@/services/DataService";
import Pager from '../../Common/Pager.vue'
import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
export default {
  components: {
    Pager
  },
  props: {
    folderid: {
      type: Number,
      required: true
    },
    reportid: {
      type: Number,
      required: true
    },
    foldername: {
      type: String,
      required: true
    },
    sourcetype:
    {
      type: String,
      required: true
    },
    sharetype:
    {
      type: String,
      required: true
    },
  },
  data() {
    return {
      isLoading: false,
      ShowLisiting: true,
      UserDDList: [],
      RoleDDList: [],
      sharesourcetype: '',
      sharesourceId: '',
      sharewithtype: 'User',
      sharewithId: '',
      sharewithUser: '',
      sharewithRole: '',
      dynamicreportShare_Id: 0,
      username: '',
      is_paged: 1,
      sortBy: '',
      sortExp: '',
      PageSize: 10,
      PageNumber: 1,
      UserListingData: [],
      access: '',
      modelaccess: '',
      accessvalue: null,
      dynamicid: null,
      Search: '',
      updatevalue: [],
      TotalPages: 0,
      TotalRecords: 0,
      userType: 'ALL',
      isChanged: false,
      sourcetype: '',
      sharerepaortId: '',
      FormSchema: [
        {
          layoutType: "Single",
          Data: [
            {
              astype: "RadioField",
              label: this.$t('Share' + ' ' + this.foldername + ' with'),
              name: "Sharewithtype",
              value: "User",
              placeholder: "",
              config: {
                options: [{
                  value: "User",
                  name: "User"
                },
                {
                  value: "Role",
                  name: "Role"
                }
                ],
                showAddIcon: false,
                onChange: this.SelectshareType
              },
              validationRules: "",
            },
          ]
        },
        {
          layoutType: "double",
          Data: [
            {
              astype: "SelectField",
              label: this.$t('User'),
              name: "User",
              value: "",
              placeholder: "Please Select User ",
              visibility: true,
              config: {
                options: [],
                showAddIcon: false,
                onChange: this.FetchData
              },
              validationRules: "required",
            },
            {
              astype: "SelectField",
              label: this.$t('Role'),
              name: "Role",
              value: '',
              placeholder: "Please Select Role",
              visibility: false,
              config: {
                options: [],
                showAddIcon: false,
                onChange: this.FetchDataDDL
              },
              validationRules: "required",
            },
            {
              astype: "SelectField",
              label: this.$t('Access'),
              name: "Access",
              value: "",
              placeholder: "Please Select Access ",
              visibility: true,
              config: {
                options: [{ value: "1", name: " View Only" }, { value: "2", name: "View & Edit" }, { value: "3", name: "Full Control" }],
                showAddIcon: false,
                onChange: ''
              },
              validationRules: "required",
            }
          ]
        },

        // {
          
        //   layoutType: "Single",
        //   Data: [
        //     {
        //       astype: "SlotField",
        //       label: this.$t('Alreadyshare'),
        //       name: "tblContainer",
        //       value: "",
        //       placeholder: "",
        //       visibility:true,
        //       config: {
        //         options: [],
        //       },
        //       validationRules: "",
        //     },
        //   ]
        // },

      ],
      buttons: [
        {
          type: "submit",
          class: "btn btn-success save",
          text: "<i class='fa fa-share-alt pr-2'></i>Share"
        }
      ]
    }
  },
  created: function () {
    var vm = this;
    vm.FetchData();
    vm.BindUserRoleListing();
  },
  methods: {
    FetchData: function () {
      var vm = this;
      if ((vm.FormSchema)[1].Data[0].name = 'User') {
        vm.isLoading = true;
        ShareDataService.UserDDL().then(response => {
          if (response.data != '' && response.data != null) {
            let fetchuserdata = [];
            fetchuserdata = response.data;
            vm.FormSchema[1].Data[0].config.options = []
            fetchuserdata.forEach((item) => {
              if(item.userid != GetUserInfo.ID){
                (vm.FormSchema)[1].Data[0].config.options.push({
                  value: item.userid,
                  name: item.username
                })
              }
            })
          }
          vm.$refs.formdata.UpdateKeyValue();
          vm.isLoading = false;
        }).catch(err => {
          console.log(err)
        })
      }
    },
    FetchDataDDL: function (e, field) {
      var vm = this;
      if ((vm.FormSchema)[1].Data[1].name == 'Role') {
        var params = `userType=${vm.userType}`;
        ShareDataService.GetRolesDDl(params).then(response => {
          if (response.data.DATA != null && response.data.DATA != "") {
            let FetchRoleData = [];
            FetchRoleData = response.data.DATA;
            vm.FormSchema[1].Data[1].config.options = []
            FetchRoleData.forEach((item, index) => {
              vm.FormSchema[1].Data[1].config.options.push({
                value: item.role_id,
                name: item.role_name
              })
            })
            vm.$refs.formdata.UpdateKeyValue();
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    SelectshareType(event, field) {
      var vm = this;
      field.value = event.target.getAttribute('data-value');
      if (field.value == 'User') {
        (vm.FormSchema)[1].Data[0].visibility = true;
        (vm.FormSchema)[1].Data[1].visibility = false;
        vm.FetchData()
      } else {
        (vm.FormSchema)[1].Data[0].visibility = false;
        (vm.FormSchema)[1].Data[1].visibility = true;
        vm.FetchDataDDL()
      }
      vm.$refs.formdata.validateForm.reset();
    },
    closePopup: function () {
      var vm = this;
      vm.$emit("close");
      vm.UserListingData = []
    },
  
      onSubmit: function (e) {
      var vm = this;
      var SharesourceID = 0;
      vm.sourcetype = (vm.$parent.sourcetype != undefined) ? vm.$parent.sourcetype : 'Report';
      if (e.Sharewithtype == 'Role') {
        vm.sharewithId = e.Role
      }
      else{
        vm.sharewithId = e.User;
      }
      if (vm.sourcetype == 'Report') {
        SharesourceID = vm.reportid
      } 
      else { SharesourceID = vm.folderid }
      if (e.Sharewithtype != '' && vm.sharewithId != "" && e.Access != "") {
        var params = `dynamicreportShare_Id=${vm.dynamicreportShare_Id}&sharesourcetype=${vm.sourcetype}&sharesourceId=${SharesourceID}&sharewithtype=${e.Sharewithtype }&sharewithId=${vm.sharewithId}&access=${e.Access}`;
        ShareDataService.ShareUserRole(params).then(function (response) {
          vm.BindUserRoleListing();
          if (response.data != null) {
            if (response.data[0].Value == 1) {
              if (vm.sourcetype == 'Report') {
                vm.ShowAlert(vm.$t("SharedSuccess", [vm.$t("Report")]), 'success', true, vm.$t('Alert'));
              }
              else {
                vm.ShowAlert(vm.$t("SharedSuccess", [vm.$t("Folder")]), 'success', true, vm.$t('Alert'));
              }
            }
            if (response.data[0].Value == 0) {
              // if (vm.sourcetype == 'Report') {
              //   vm.ShowAlert('Report is already shared with selected user', 'danger', true, vm.$t('Alert'));
              // }
              // else {
              //   vm.ShowAlert('Folder is already shared with selected user', 'danger', true, vm.$t('Alert'));
              // }
              vm.ShowAlert('Report is already shared with selected ' + e.Sharewithtype, 'danger', true, vm.$t('Alert'));
            }
            e.Sharewithtype = 'User';
            vm.FormSchema[1].Data[0].value='';
            vm.FormSchema[1].Data[1].value='';
            vm.FormSchema[1].Data[2].value='';
            vm.$refs.formdata.validateForm.reset();
          }
        })

      }
    },
    BindUserRoleListing: function () {
      var vm = this;
      var SharesourceID = 0;
      if (vm.sharetype == 'sharereport') {
        SharesourceID = vm.reportid
      } else { SharesourceID = vm.folderid }
      var params = `username=${vm.username}&is_paged=${vm.is_paged}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&shared_Id=${SharesourceID}`;
      console.log('params', params);
      ShareDataService.ShareUserRoleListing(params).then(function (response) {
        vm.UserListingData = response.data;
        if (response.data.length > 0) {
          vm.TotalRecords = response.data[0].totaL_RECORDS;
          vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
          if (vm.isLoading) {
            vm.isLoading = true
          } else { vm.isLoading = false }
          // vm.isLoading = false;
        }
        else {
          vm.TotalRecords = 0;
          vm.TotalPages = 0;
        }
      })
    },
    SearchData: function () {
      var vm = this;
      var SharesourceID = 0;
      if (vm.sharetype == 'sharereport') {
        SharesourceID = vm.reportid
      } 
      else { 
        SharesourceID = vm.folderid 
      }
      var params = `username=${vm.Search}&is_paged=${vm.is_paged}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&shared_Id=${SharesourceID}`;
      ShareDataService.ShareUserRoleListing(params).then(function (response) {
        vm.UserListingData = response.data;
        if (vm.UserListingData.length == 0) {
          vm.TotalRecords = [];
        }
        else {
          vm.TotalRecords = response.data[0].totaL_RECORDS;
        }
      })
    },
    Updateshare: function () {
      var vm = this;
      if (vm.sharewithUser == 'User') {
        vm.sharewithId = vm.sharewithUser;
      }
      else if (vm.sharewithUser == 'Role') {
        vm.sharewithId = vm.sharewithRole;
      }
      else if (vm.isChanged == true) {
        var params = `dynamicreportShare_Id=${vm.dynamicid}&sharesourcetype=Folder&sharesourceId=${vm.folderid}&sharewithtype=${vm.sharewithtype}&sharewithId=${vm.sharewithId}&access=${vm.accessvalue}`;
        ShareDataService.UpdateShareUserRole(params).then(function (response) {
          if (response.data[0].Status == "Update") {
            if (vm.sourcetype == 'Report') {
              vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Share Report")]), 'success', true, vm.$t('Alert'))
            }
            else {
              vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Share Folder")]), 'success', true, vm.$t('Alert'))
            }
            vm.sharewithId = "";
            vm.sharewithUser = "";
            vm.sharewithUser = "";
          }
          vm.BindUserRoleListing();
        })
      }
      vm.isChanged = false;
    },

    dropvalue: function (event, dynamicreportShare_Id) {
      var vm = this;
      vm.isChanged = true;
      vm.accessvalue = event.target.value;
      vm.dynamicid = dynamicreportShare_Id;
    },
    pagerMethod: function (e) {
      var vm = this;
      if (e.target.innerHTML == 'Prev') {
        vm.PageNumber = vm.PageNumber - 1;
      }
      else if (e.target.innerHTML == 'Next') {
        vm.PageNumber = parseInt(vm.PageNumber) + 1;
      }
      else {
        vm.PageNumber = e.target.innerHTML;
      }
      if (vm.PageNumber == "First") {
        vm.PageNumber = "1";
      }
      if (vm.PageNumber == "Last") {
        vm.PageNumber = vm.TotalPages
      }
      vm.PageSize = vm.PageSize;
      vm.PageNumber = vm.PageNumber;
      vm.BindUserRoleListing();
    },
    SetPageSize: function (event) {
      var vm = this;
      vm.isLoading = true;
      vm.TotalRecords = 0;
      vm.PageSize = event.target.value;
      vm.PageNumber = 1;
      vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
      vm.BindUserRoleListing();
    },
    shareroleListingDelete: function (id) {
      var vm = this;
      if (id > 0) {
        vm.sourcetype = (vm.$parent.sourcetype != undefined) ? vm.$parent.sourcetype : 'Report';
        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t(vm.sourcetype)), function () {
          var dynamicreportShare_Id = `dynamicreportShare_Id=${id}`
          ShareDataService.DeleteShareUserRole(dynamicreportShare_Id).then(function (response) {
            if (response.data[0].Status == "Success") {
              vm.ShowAlert(vm.$t("UserRemoved"), 'success', true, vm.$t('Alert'));
              vm.BindUserRoleListing();
            }
            else {
              vm.ShowAlert(vm.$t("DeletedError", [vm.$t(vm.sourcetype)]), 'failure', true, vm.$t('Alert'));
            }
          });
        })
      }
    },
  }
}
</script>
<template>
  <div id="modalshareview" role="dialog" class="modal my-popups right fade" v-cloak>
    <div role="document" class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header mheader-user">
          <button type="button" class="close" v-on:click="closePopup()">
            <em aria-hidden="true" class="fa fa-times"></em>
          </button>
          <h4 id="myModalLabel" class="modal-title">{{ $t("Share") }}</h4>
          <span class="user-guide">
            <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button" aria-expanded="false"
              aria-controls="collapseExample"> <em></em>User Guide</a>
            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
              <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                <div v-if="$parent.sourcetype == 'folder'" class="con" v-html="$t('User_Guide_Folder_Share')"></div>
                <div v-else class="con" v-html="$t('User_Guide_Report_Share')"></div>
              </div>
            </div>
          </span>
        </div>
        <div class="modal-body">
          <!-- <loader :is-visible="isLoading"></loader> -->
          <dynamic-form  ref="formdata" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
              <!-- <template  v-slot:tgslot-tblContainer="{ data }">
                    
                
              </template>   -->
          </dynamic-form>
          <div class="w-100 float-left mt-2" >
                    <div class="heading-border">
                    <h5 class="h5">
                      <span>{{ $t("SharedAlready") }}</span>
                    </h5>
                  </div>

                  <div class="col-12 p-0 mt-2 px-small-0">
                  <div class="form-group mb-3">
                    <input id="searchdata" maxlength="50" v-model="Search" class="form-control" type="text"
                      v-on:keyup="SearchData()" placeholder="Search">
                  </div>
                </div>
                <div class="table-responsive table-fix-header mb-3">
                  <table id="example" class="table table-bordered mt-0 mb-0" style="width:100%"
                    aria-label="ShareUserRole listing">
                    <!-- <thead>
                      <th scope="col"> </th>
                    </thead> -->
                    <tbody>
                      <tr v-for="item in UserListingData" :key="item">
                        <td>
                          <div class="conversation-box_custreps">
                            <div class="name-square" v-if="(item.sharewith_type == 'User')">
                              <em class="fa fa-user text-white fa-2x" aria-hidden="true"></em>
                            </div>
                            <div class="role-square" v-if="(item.sharewith_type == 'Role')">
                              <em class="fa fa-user-circle text-white fa-2x" aria-hidden="true"></em>
                            </div>
                            <div class="conversation-header">
                              {{ item.username }}
                            </div>
                            <div class="conversation-header">
                              <small>
                                {{ item.sharewith_type }}
                              </small>
                            </div>
                          </div>
                        </td>
                        <td class="text-right pr-3 single-action">
                          <select @change="dropvalue($event, item.dynamicreportShare_Id)" class="form-control w-60 float-left">
                            <option value="1" v-bind:selected="item.access == 1">View Only</option>
                            <option value="2" v-bind:selected="item.access == 2">View & Edit</option>
                            <option value="3" v-bind:selected="item.access == 3">Full Control</option>
                          </select>
                          <a class="float-right mt-2" href="javascript:;" :title="$t('Delete')"
                            v-on:click="shareroleListingDelete(item.dynamicreportShare_Id)">
                            <em class="fa fa-trash text-danger ml-4"></em>
                          </a>
                        </td>
                      </tr>
                      <tr v-if="UserListingData.length < 1" class="border-0">
                        <td colspan="2" class="text-center text-danger no-record">No Record(s) Found.</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <Pager v-bind:numberofrecords="TotalRecords" prev-text="Prev" first-text="First" last-text="Last"
                  next-text="Next" @click="pagerMethod" v-on:change="SetPageSize"></Pager>
                </div>
                <div class="col-12 float-left p-0 mt-3">
                <div class="row flex-row-reverse">
                  <div class="col-lg-6 text-right" v-if="(TotalRecords > 0)">
                    <a href="javascript:;" :class="{ 'btn btn-primary ancsaveuserticket': true, 'disabled': !isChanged }"
                      id="btnSaveDraft" title="" data-toggle-tooltip="tooltip" @click="Updateshare()"><em
                        class="fa fa-check pr-2"></em>Done</a>
                  </div>
                </div>
              </div>
        </div>
      </div>
    </div>
  </div>
</template>
