<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" :showCheckBox="true" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting"
            IdentityColumn="REPORT_ID" :HeaderText="$t('Custom Reports')" :ListData="ReportData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
            :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
          
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'"  >
                <listingstatus v-if="StatusListForListing.length > 0 && data.row.Is_System_Report == false" :isfor="'Dynamic_Reporting'"  
                v-bind:dataid="data.row.REPORT_ID"   
                data-controller="Report" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="Index" v-bind:dataselected="data.row.STATUS_NAME" 
                v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                </listingstatus>
                 <a class="inactive-disabled" v-if="data.row.Is_System_Report == true"> 
                    <span v-bind:class="getStatusClass(data.row.STATUS_NAME, 'STATUS_NAME')">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                 </a> 
            </template>
        </template>
        </tg-list>
        <!---modal popup-->
        <viewmanagefolder v-if="ShowManageView" v-show="ShowManageView"
            ref="managefolder" v-on:close="ShowManageView = false">
        </viewmanagefolder>
        <viewcustomreport v-if="ShowAddCustomReportView" ref="customreportfolder" v-on:close="ShowAddCustomReportView = false"
            :ReportID="ReportID"></viewcustomreport>
        <sharereportview v-if="showsharereportview" :folderid="folderid" :foldername="foldername" :sourcetype="report"
            :sharetype="sharetype" :reportid="ReportID" v-on:close="closeModalSharePopup"></sharereportview>
        <emailreportview v-if="showemailreportview" v-on:close="showemailreportview = false" :ReportID="ReportID">
        </emailreportview>
        <modalscheduleview v-if="showschedulereportview" v-on:close="showschedulereportview = false" :ReportID="ReportID">
        </modalscheduleview>
    </div>
</template>
<script>
import ReportDataService from "@/services/DataService";
import Schema from "@/views/Reporting/Customreportschema";
import viewmanagefolder from '../../components/Common/Modal/modalmanagefolder.vue'
import viewcustomreport from '../../components/Common/Modal/modalAddCustomReport.vue'
import sharereportview from '../../components/Common/Modal/modalshare.vue'
import emailreportview from '../../components/Common/Modal/modalemailreport.vue'
import modalscheduleview from '../../components/Common/Modal/modalschedule.vue';
import listingstatus from '../../components/Common/ListingStatus.vue';

export default {
    components: {
        sharereportview,
        viewcustomreport,
        viewmanagefolder,
        emailreportview,
        modalscheduleview,
        listingstatus
    },
    props: {
        sourcetype:
        {
            type: String,
            required: true
        }
    },
    data() {
        return {
            noRecord:false,
            isLoading: false,
            ReportData: null,
            TotalRecords: 0,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            ShowManageView: false,
            ShowAddCustomReportView: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            listActions: Schema.listActions,
            searchCondition: '',
            conditionForInstantSearch: "",
            SortBy: '',
            SortExp: '',
            showsharereportview: false,
            showemailreportview: false,
            showschedulereportview: false,
            ReportID: null,
            reportname: '',
            reportid: '',
            foldername: '',
            folderid: '',
            LeadStatusList: [],
            StatusListForListing:[],
            searchcondition: '',
            MODULENAME: null,
            SUBMODULECODE: null,
            privilegeParams: null,
            sharetype: '',
            Headers: [
                {
                    COLUMN_NAME: "REPORTNAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Report Name",
                    DISPLAY_ORDER: 1,
                }, {
                    COLUMN_NAME: "MODULENAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Module Name",
                    DISPLAY_ORDER: 1,
                }, {
                    COLUMN_NAME: "SUBMODULENAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Sub Module Name",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "CREATED_BY",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Created By",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "FOLDER_NAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Folder Name",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "CREATED_AT",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Created Date",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "MODIFIED_BY",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Modified By",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "MODIFIED_AT",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Modified Date",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "STATUS_NAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 1,
                },
            ],
        }
    },
    async created() {
        var ths = this;
        ths.privilegeParams = `controller=Report&action=`;
        await ths.FetchData();
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'FOLDER_NAME')
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'CREATED_BY')
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'MODIFIED_BY')
        ths.getStatuslistForListing("Dynamic_Reporting", "en");
    },
    mounted() {
        Schema.leftsearchSchema.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'View':
                        action.isVisible = row.ACCESS != null ? true : true;
                        break;
                    case 'Send Email':
                        action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 && !row.Is_System_Report ? true : false;
                        break;
                    case 'Schedule':
                        action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 && !row.Is_System_Report ? true : false;
                        break;
                    case 'Share':
                        action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 && !row.Is_System_Report ? true : false;
                        break;
                    case 'Edit':
                        action.isVisible = row.ACCESS != 1 && !row.Is_System_Report ? true : false;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "ManageFolder":
                    this.ManageFolder();
                    break;
                case "AddCustomReport":
                    this.AddCustomReport();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "EmailScheduleListing":
                    this.EmailScheduleListing();
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'sharereport':
                    var vm = this;
                    ReportDataService.CheckPrivilege(vm.privilegeParams+'ShareReport').then((response)=>{
                        if(response.data){
                            vm.sharetype = callbackfunction
                            vm.folderid = item.FOLDER_ID;
                            vm.ReportID = item.REPORT_ID;
                            vm.foldername = item.REPORTNAME;
                            vm.showsharereportview = true;
                            $("#modalshareview").modal('show');
                            setTimeout(function () {
                                $("#modalshareview").modal('show');
                                setTimeout(function () {
                                    vm.SetBootstraptoolTip();
                                }, 200);
                            }, 1000);
                        }
                        else{
                            vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                            return false;
                        }
                    });
                    break;
                case 'sendemail':
                    vm.sendemail(item.REPORT_ID, item.MODULENAME, item.SUBMODULECODE);
                    break;
                case 'editreport':
                    vm.editreport(item);
                    break;
                case 'setschedule':
                    vm.setschedule(item.REPORT_ID);
                    break;
                case 'ViewReport':
                    vm.ViewReport(item.REPORT_ID, item.ACCESS, item.FOLDER_ID,item.Is_System_Report);
                    break;
            }
        },
        editreport: function (data) {
            var vm = this;
            ReportDataService.CheckPrivilege(vm.privilegeParams+'Update').then((response)=>{
                if(response.data){
                    vm.$router.push({
                        name: 'EditDataReport',
                        params: {
                            Modulename: data.MODULENAME, Submodulecode: data.SUBMODULECODE, ReportName: data.REPORTNAME, ReportID: data.REPORT_ID,
                            Module_Id: data.MODULE_ID, Folder_Id: data.FOLDER_ID, Sub_Module_Id: data.Sub_Module_Id, Version: vm.Version, Action: 'Edit'
                        }
                    });
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        ViewReport: function (ReportID, ACCESS, FolderID,IsSystemReport) {
            var vm = this;
            vm.$router.push('/Report/viewcustomreport/' + btoa(JSON.stringify({ ReportID: ReportID, ACCESS: ACCESS , Folder_Id: FolderID, IsSystemReport: IsSystemReport })))

        },
        sortdata: async function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            await this.FetchData();
            $("#th-" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.FetchData();
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }

            await this.FetchData();
        },
        ManageFolder: function () {
            var vm = this;
            ReportDataService.CheckPrivilege(vm.privilegeParams+'FolderListing').then((response)=>{
                if(response.data){
                    vm.ShowManageView = true;
                    $("#modalManageView").modal('show');
                    setTimeout(function () {
                        $("#modalManageView").modal('show');
                        setTimeout(function () {
                            vm.SetBootstraptoolTip();
                        }, 200);
                    }, 1000);
                    vm.$refs.managefolder?.GetViewList();
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        AddCustomReport: function () {
            var vm = this;
            ReportDataService.CheckPrivilege(vm.privilegeParams+'Add').then((response)=>{
                if(response.data){
                    vm.ReportID = "";
                    vm.ShowAddCustomReportView = true;
                    $("#CustommodalManageView").modal('show');
                    setTimeout(function () {
                        $("#CustommodalManageView").modal('show');
                        setTimeout(function () {
                            vm.SetBootstraptoolTip();
                        }, 200);
                    }, 1000);
                    vm.$refs.customreportfolder?.folderlisting();
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        EmailScheduleListing() {
            var vm = this;
            vm.$router.push({ name: 'EmailScheduleListing' });
        },
        commonDeleteData: function (event) {
            var vm = this;
            ReportDataService.CheckPrivilege(vm.privilegeParams+'Delete').then((response)=>{
                if(response.data){
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: "Dynamic_Reporting",
                                subModuleCode: "Dynamic_Reporting",
                                refCode: "Dynamic_Reporting"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            ReportDataService.CommonDeleteData(objectPostString).then(async function (response) {
                                var box = "";
                                if (response.status == 200) {
                                    $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                    $("#DeleteMultiple").addClass("disabled");
                                    box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                    vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                    if (vm.PageNumber <= 0) {
                                        vm.PageNumber = 1;
                                    }
                                    await vm.FetchData();
                                }
                                else {
                                    box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                }

                            },);
                        });
                    }
                    else {
                        ShowAlert(vm.$t("Selectanyrecordtodelete"));
                    }
                }
                else
                {
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            })
        },
        sendemail: function (value, MODULENAME, SUBMODULECODE) {
            var vm = this;
            ReportDataService.CheckPrivilege(vm.privilegeParams+'SendEmail').then((response)=>{
                if(response.data){
                    vm.ReportID = value;
                    vm.MODULENAME = MODULENAME;
                    vm.SUBMODULECODE = SUBMODULECODE
                    vm.showemailreportview = true;
                    $("#modalemailview").modal('show');
                    setTimeout(function () {
                        $("#modalemailview").modal('show');
                        setTimeout(function () {
                            vm.SetBootstraptoolTip();
                        }, 200);
                    }, 1000);
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        setschedule: function (value) {
            var vm = this;
            ReportDataService.CheckPrivilege(vm.privilegeParams+'ScheduleReport').then((response)=>{
                if(response.data){
                    vm.ReportID = value;
                    vm.showschedulereportview = true;
                    $("#modalscheduleview").modal('show');
                    setTimeout(function () {
                        $("#modalscheduleview").modal('show');
                        setTimeout(function () {
                            vm.SetBootstraptoolTip();
                        }, 200);
                    }, 1000);
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        getRequestParams: function (pageNumber, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["is_paged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        clickEventOfColumn: function (row, column) {
            if (column.COLUMN_NAME == 'REPORTNAME') {
                this.$router.push({ name: 'viewcustomreport', params: { ReportID: row.REPORT_ID, ACCESS: row.ACCESS } });
            }
        },
        FetchData: async function () {
            const params = this.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
            var vueObj = this;
            this.isLoading = true;
            params.pageSize = vueObj.PageSize;
            params.pageNumber = vueObj.PageNumber;
            params.searchCondition = vueObj.searchCondition;
            if (vueObj.PageNumber) { params.pageNumber = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNumber = 1; }
            if (typeof vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
            else { vueObj.isPaged = params.isPaged = 1; }
            params.isPaged = vueObj.isPaged;
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            var srchCondition = "";
            vueObj.TotalRecords = vueObj.TotalRecords - 1;
            if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            await ReportDataService.CustomReportListing(params)
                .then(function (response) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "REPORTNAME") {
                            column.settings = {
                                clickEvent: vueObj.clickEventOfColumn,
                                formatter: vueObj.columnDataFormatter,
                            };
                        }
                        if (column.COLUMN_NAME == "STATUS_NAME") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    if (response.data != "" && response.data != null) {
                        if (response.data.data.length > 0) {
                            vueObj.ReportData = response.data.data;
                            vueObj.ReportData.forEach(ritem => {
                                ritem.SUBMODULENAME=vueObj.$t(ritem.SUBMODULENAME);
                                if (ritem.ACCESS == 1 || ritem.ACCESS == 2 || ritem.Is_System_Report == true
                                ) {
                                    ritem.isCheckBoxDisabled = true;
                                } else { ritem.isCheckBoxDisabled = false }
                                if (ritem.ACCESS == null) { ritem.ACCESS = 0 }
                                ritem.rowDetailsUrl = '';
                                if (ritem.rowDetailsUrl == '') {
                                    // ritem.rowDetailsUrl='/viewcustomreport/' + ritem.REPORT_ID +'/'+ ritem.ACCESS;
                                    ritem.rowDetailsUrl = '/Report/viewcustomreport/' + btoa(JSON.stringify({ ReportID: ritem.REPORT_ID, ACCESS: ritem.ACCESS, Folder_Id: ritem.FOLDER_ID,  IsSystemReport: ritem.Is_System_Report }));
                                }
                            })
                            vueObj.TotalRecords = response.data.data[0].TOTAL_RECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.ReportData = [];
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                        }
                        if (localStorage.SavedParameters != undefined) {
                            localStorage.removeItem("SavedParameters");
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ReportData = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () {
                        vueObj.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vueObj.isLoading = false;
                });
        },
        closeModalSharePopup: function () {
            $("#modalshareview").modal('hide');
            this.showsharereportview = false;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url ="langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                ReportDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        }
    }
}
</script>