<script>
import CustomDataService from "@/services/DataService";
import sharefolder from '../Modal/modalshare.vue'
import editmodel from '../Modal/modaleditfolder.vue'
import Pager from '../../Common/Pager.vue'
export default {
  components: {
    sharefolder,
    editmodel,
    Pager
  },
  props: {
    callbackfunction: {
      type: Function,
    },
    show: {
      type: Boolean,
    },
    usertype: {
      type: String,
    },
  },
  data: function () {
    return {
      isLoading: false,
      // ShowLisiting: true,
      ListingData: [],
      PageNumber: 1,
      PageSize: 10,
      SortBy: "",
      SortExp: "",
      noRecord: true,
      editmodal: false,
      foldername: '',
      is_paged: 1,
      GroupData: [],
      folderId: null,
      TotalRecords: 0,
      TotalPages: 0,
      folderid: "",
      sourcetype: "folder",
      showsharereportview: false,
      isDeleteMultiple: false,
      privilegeParams: null,
      Headers: [
        {
          COLUMN_NAME: "foldeR_NAME",
          DATA_TYPE: "text",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: "FoldeR NAME",
          DISPLAY_ORDER: 1,
        }, {
          COLUMN_NAME: "createD_BY",
          DATA_TYPE: "text",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: "Created By",
          DISPLAY_ORDER: 2,
        }
      ],
      listheaderbuttons: [{
        title: 'Add New Folder',
        iconClass: 'fa-folder',
        callbackfunction: 'Addnewfolder',
        additionalClass: '',
        isdisabled: false,
        isvisible: true,
        checkPrivilege: false
      },
      {
        id: 'DeleteMultiple',
        title: 'Delete',
        iconClass: 'fa-trash-o',
        callbackfunction: 'commonDeleteData',
        isdisabled: true,
        isvisible: true,
      }
      ],
      searchCondition: ''
    };
  },
  created: function () {
    var vm = this;
    vm.privilegeParams = `controller=Report&action=`;
    vm.GetViewList();
  },
  mounted: function () {
    $('#modalManageView').modal({
      backdrop: 'static',
      keyboard: false
    })
  },
  methods: {
    close: function () {
      var vm = this;
      vm.$parent.ShowManageView = false;
      $("#modalManageView").modal("hide");
      $("body").removeClass("modal-open");
      $(".modal-backdrop").remove();
    },
    //Listing on ManageFolder
    GetViewList: function () {
      var vm = this;
      vm.foldername = '';
      var params = `foldername=${vm.foldername}&is_paged=${vm.is_paged}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
      CustomDataService.ManagefolderListing(params).then(
        function (response) {
          if (response.data != null && response.data != "") {
            vm.ListingData = response.data;
            vm.TotalRecords = response.data[0].totaL_RECORDS;
            vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
            vm.isLoading = false;
            vm.noRecord = false;
          }
        },
      );
      setTimeout(function () {
        vm.ResponsiveDataTable('tablelistingdata')
      }, 500);
    },
    showshare: function (foldeR_ID, foldeR_NAME) {
      var vm = this;
      vm.showsharereportview = !vm.showsharereportview;
      vm.folderid = foldeR_ID;
      vm.foldername = foldeR_NAME;
      vm.showsharereportview = true;
      $("#modalshareview").modal('show');
      setTimeout(function () {
        $("#modalshareview").modal('show');
        setTimeout(function () {
          vm.SetBootstraptoolTip();
        }, 200);
      }, 1000);
    },
    // For opening Edit Folder and Passing FolderID
    editfolder: function (foldeR_ID) {
      var vm = this;
      vm.folderId = foldeR_ID;
      vm.editmodal = true;
      $("#modaleditview").modal('show');
      setTimeout(function () {
        $("#modaleditview").modal('show');
        setTimeout(function () {
          vm.SetBootstraptoolTip();
        }, 200);
      }, 1000);
    },
    Addnewfolder: function () {
      var vm = this;
      CustomDataService.CheckPrivilege(vm.privilegeParams + 'AddFolder').then((response) => {
        if (response.data) {
          vm.folderId = ""
          vm.editmodal = true;
          $("#modaleditview").modal('show');
          setTimeout(function () {
            $("#modaleditview").modal('show');
            setTimeout(function () {
              vm.SetBootstraptoolTip();
            }, 200);
          }, 1000);
        }
        else {
          vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
          return false;
        }
      });
    },
    DeleteActivityData: function (id, foldeR_NAME, associateD_REPORT) {
      var vm = this;
      var reportname = foldeR_NAME;
      if (associateD_REPORT > 0) {
        vm.ShowAlert(vm.$t('AssociatedReport', [associateD_REPORT]), 'warning', true, vm.$t('Alert'));
        return false;
      }
      if (id > 0) {
        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Folder Name")), function () {
          var folder_id = `folder_id=${id}`
          CustomDataService.DeleteReportFolder(folder_id).then(function (response) {
            if (response.data[0].Status == "Success") {
              vm.ShowAlert(vm.$t("DeletedSuccess", [reportname + " folder"]), 'success', true, vm.$t('Alert'));
              vm.GetViewList();
            }
            else {
              vm.ShowAlert(vm.$t("DeletedError", [vm.$t("Folder Name")]), 'failure', true, vm.$t('Alert'));
            }

          });
        })
      }
      else {
        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), 'warning', true, vm.$t('Alert'));
      }
    },
    //For Pagination
    pagerMethod: function (e) {
      var vm = this;
      // if (e.target.innerHTML == 'Prev') {
      //   vm.PageNumber = vm.PageNumber - 1;
      // }
      // else if (e.target.innerHTML == 'Next') {
      //   vm.PageNumber = parseInt(vm.PageNumber) + 1;
      // }
      // else {
      //   vm.PageNumber = e.target.innerHTML;
      // }
      // if (vm.PageNumber == "First") {
      //   vm.PageNumber = "1";
      // }
      // if (vm.PageNumber == "Last") {
      //   vm.PageNumber = vm.TotalPages
      // }
      vm.PageNumber = e;
      vm.GetViewList();
    },
    SetPageSize: function (event) {
      var vm = this;
      vm.isLoading = true;
      vm.TotalRecords = 0;
      vm.PageSize = event;
      vm.PageNumber = 1;
      vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
      vm.GetViewList();
    },
    closeModalSharePopup: function () {
      var vm = this;
      $("#modalshareview").modal('hide');
      vm.showsharereportview = false;
    },
    onChange: function () {
      var checkCount = $('.chkItemsManage:checked').not("[id^='chkAllManage']").length;
      if (checkCount > 0) {
        this.isDeleteMultiple = true;
      }
      else {
        this.isDeleteMultiple = false;
      }
    },
    commonDeleteData: function (event) {
      var vm = this;
      var val = "";
      $('.chkItemsManage:checkbox:checked').not("[id^='chkAllManage_0']").each(function () {
        if (val.length > 0)
          val += ',';
        val += $(this).val();
      });
      if (val.length > 0) {
        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Folder")), function () {
          var postJSON = {
            ids: val,
            moduleName: "Dynamic_Reporting",
            subModuleCode: "Dynamic_Reporting",
            refCode: "Dynamic_Reporting_Folder"
          };
          var objectPostString = JSON.stringify(postJSON);
          CustomDataService.CommonDeleteData(objectPostString).then(function (response) {
            if (response.status == 200) {
              $('.chkItemsManage:checkbox:checked,#chkAllManage_0').prop("checked", false).removeAttr('checked');
              vm.isDeleteMultiple = false;
              vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Folder')]), "success", true, vm.$t("Alert"));
              vm.PageNumber = parseInt(vm.PageNumber) - 1;
              if (vm.PageNumber <= 0) {
                vm.PageNumber = 1;
              }
              vm.GetViewList();
            }
            else {
              vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Folder')]), "success", true, vm.$t("Alert"));
            }

          },);
        });
      }
      else {
        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
      }
    },
    SetCurrentPage(e) {
      var vm = this;
      if (e.PagerMethod == 'a') {
        vm.pagerMethod(e.PageNumber);
      }
      else if (e.PagerMethod == 'b') {
        vm.SetPageSize(parseInt(e.PageSize));
      }

    },
    renderActions() { },
    leftCommonSearch() { },
    pagerMethod: async function (value) {
      this.PageSize = value.PageSize;
      this.PageNumber = value.PageNumber;
      await this.GetViewList();
    },
    commonHeaderButtonClick: function (event) {
      switch (event.callbackfunction) {
        case "Addnewfolder":
          this.Addnewfolder();
          break;
        case "commonDeleteData":
          this.commonDeleteData();
          break;
      }
    },
    actionButtonClick() { },
    sortdata() { },
  }
}
</script>
<template>
  <div id="modalManageView" role="dialog" class="modal my-popups right fade" v-cloak>
    <div role="document" class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header mheader-user">
          <button type="button" class="close" v-on:click="close()">
            <em aria-hidden="true" class="fa fa-times"></em>
          </button>
          <h4 id="myModalLabel" class="modal-title">{{ $t("ManageFolderName") }}</h4>
          <span class="user-guide">
            <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button" aria-expanded="false"
              aria-controls="collapseExample"> <em></em>User Guide</a>
            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
              <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                <div class="con" v-html="$t('User_Guide_Dynamic_Manage_Folder')"></div>
              </div>
            </div>
          </span>
        </div>
        <div class="modal-body">
          <loader :is-visible="isLoading"></loader>
          <div class="w-100 float-left">
            <div class="float-right">
              <a class="btn btn-info my-2 my-sm-0" @click="Addnewfolder()" :title="$t('AddNewFolder')">
                <em class="fa fa-folder pr-2"></em>
                {{ $t("AddNewFolder") }}
              </a>
              <a id="DeleteMultipleManage"
                :class="{ 'btn btn-danger my-2 my-sm-0 ml-2': true, 'disabled': !isDeleteMultiple }"
                @click="commonDeleteData()" :title="$t('Delete')">
                <em class="fa fa-trash pr-2"></em>
                {{ $t("Delete") }}
              </a>
            </div>


            <div class="listing mt-3">
              <div class="table-responsive table-fix-header">
                <table id="tablelistingdata" class="table table-bordered mt-0" style="width:100%"
                  aria-label="Folder Listing">
                  <thead class="thead-bg">
                    <tr>
                      <th style="width:50px !important;" class="text-center"><input id="chkAllManage" class="chkitems"
                          value="" type="checkbox" v-on:change="onChange()"></th>
                      <th scope="col">{{ $t("FolderName") }}</th>
                      <th scope="col">{{ $t("CreatedBy") }}</th>
                      <th scope="col" class="text-center" width="200">{{ $t("Actions") }}</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in ListingData" :key="item">

                      <td class="text-center"><input class="chkItemsManage" v-bind:value="item.foldeR_ID"
                          :disabled="item.associateD_REPORT > 0" type="checkbox" v-on:change="onChange()"></td>
                      <td>{{ item.foldeR_NAME }}</td>
                      <td>{{ item.createD_BY }}</td>
                      <td class="text-center single-action ">
                        <a href="javascript:;" :title="$t('Share')"
                          v-on:click="showshare(item.foldeR_ID, item.foldeR_NAME)"
                          :class="item.access == 1 || item.is_System_Folder == true ? 'd-none' : ''">
                          <em class="fa fa-share-alt text-primary "></em>
                        </a>
                        &emsp;
                        <a href="javascript:;" :title="$t('Edit')" v-on:click="editfolder(item.foldeR_ID)"
                          :class="item.access == 1 || item.is_System_Folder == true ? 'd-none' : ''">
                          <em class="fa fa-pencil text-success"></em>
                        </a>&emsp;
                        <a href="javascript:;" :title="$t('Delete')"
                          :class="item.access == 1 || item.access == 2 || item.is_System_Folder == true ? 'd-none' : ''"
                          v-on:click="DeleteActivityData(item.foldeR_ID, item.foldeR_NAME, item.associateD_REPORT)">
                          <em class="fa fa-trash text-danger"></em>
                        </a>
                      </td>
                    </tr>
                    <tr v-if="noRecord" style="cursor:initial;">
                      <td colspan="4">
                        <div class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                          {{ $t("NoRecordfound") }}</div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <!-- <Pager v-bind:numberofrecords="TotalRecords" prev-text="Prev" first-text="First" last-text="Last"
                next-text="Next" @click="pagerMethod" v-on:change="SetPageSize"></Pager> -->
              <Pager v-bind:numberofrecords="TotalRecords" prev-text="Prev" first-text="First" last-text="Last"
                next-text="Next" @SetCurrentPage="SetCurrentPage"></Pager>
            </div>
          </div>

          <!-- <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Dynamic_Reporting"
            SubModuleCode="Dynamic_Reporting" IdentityColumn="foldeR_ID" :HeaderText="$t('Custom Reports')"
            :ListData="ListingData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false" :SearchFields="false"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="GetViewList"
            :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch"
            :LegendArray="[]" sourcetype="" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">           

          </tg-list> -->

        </div>
      </div>
    </div>
    <section class="tab mt-2">
      <sharefolder v-if="showsharereportview" :foldername="foldername" :sourcetype="folder"
        @close="closeModalSharePopup" :folderid="folderid"></sharefolder>
    </section>
    <section class="tab mt-2">
      <editmodel :folderId="folderId" v-if="editmodal" @close="editmodal = false"></editmodel>
    </section>
  </div>
</template>
