function bindleftCommonSearchdropdown(ths, modulename, filtername) {
    var vm = ths;
    let url=filtername;
    let params = {};
    params["moduleName"] = modulename; 
    params["type"] = filtername
   ReportDataService.GetMainReportSearching(url)
        .then(function (response) {
            console.log(leftsearchSchema);
            var json = response.data;
            console.log('folderid', json);
            if (json != null) {
                if (filtername == "FOLDER_NAME") {
                        vm.leftSearchFields[4].listOptions =json.map((item) => {
                        const container = {};                    
                        container.name = item.FOLDER_NAME;
                        container.value = item.FOLDER_ID.toString();                    
                        return container;
                    })                   
                        vm.LeadStatusList = json.map((items) => {
                        const container = {};                    
                        container.name = items.FOLDER_NAME;
                        container.value = items.FOLDER_ID.toString();                    
                        return container;
                    })                    
                }  
                if (filtername == "CREATED_BY") {
                        vm.leftSearchFields[3].listOptions =json.map((value) => {
                        const container = {};                    
                        container.name = value.CREATED_BY;
                        container.value = value.CREATED_ID.toString();                    
                        return container;
                    })                   
                        vm.LeadStatusList = json.map((values) => {
                        const container = {};                    
                        container.name = values.CREATED_BY;
                        container.value = values.CREATED_ID.toString();                    
                        return container;
                    }) 
                } 
                if (filtername == "MODIFIED_BY") {
                        vm.leftSearchFields[7].listOptions =json.map((data) => {
                        const container = {};                    
                        container.name = data.MODIFIED_BY;
                        container.value = data.MODIFIED_ID.toString();                    
                        return container;
                    })                   
                        vm.LeadStatusList = json.map((dataval) => {
                        const container = {};                    
                        container.name = dataval.MODIFIED_BY;
                        container.value = dataval.MODIFIED_ID.toString();                    
                        return container;
                    }) 
                }   
               

            }
        
    });

};
export default { buttons, leftsearchSchema, listActions, bindleftCommonSearchdropdown };
