            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FolderName'),
                            name: "FolderName",
                            value: "",
                            placeholder: "Enter Folder Name",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "required",
                        },
                       
                    ]
                },
                {
                    layoutType: "single", 
                    Data:[
                    {
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
      
                            placeholder: "Enter Description ",
                            config: {
                                options: [],
                                showAddIcon: false,
                                cols:6,
                                rows:7
                            },
                            validationRules: "required",
                     },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                }
            ]
    }
  },
  created: function () {
    var vm = this;
    // Receiving folderid on Edit
    vm.editFolder(vm.folderId);
    if(vm.folderId>0){
      vm.editfoldername="Edit Folder"
    }
    else{
      vm.editfoldername="Add Folder"
    }
  },
  methods: {
    close: function () {
      var vm = this;
      vm.$parent.editmodal = false;
      vm.foldernamemodel = "";
      vm.folderdescmodel = "";
    },
    // SaveFolder: function () {
