    //           vm.close();
    //           vm.$parent.GetViewList();
    //         }
    //       }).catch(err => {
    //         console.log(err)
    //       });
    //     }
    //   })
    // },
    editFolder: function (folderId) {
      var vm = this;
      var params = `&folder_Id=${folderId}`;
      console.log('params', params);
      CustomDataService.EditReportFolder(params).then(response => {
        console.log('response', response);
        vm.FormSchema[0].Data[0].value = response.data.folderName,
         vm.FormSchema[1].Data[0].value = response.data.description
      });
    },
    onSubmit: async function(e){
            var vm = this;
            // vm.SaveDynamicreport(ReportID)     
          
          if (vm.folderId == '') {
            var params = `foldername=${e.FolderName}&description=${e.Description}`;
          }
          else {
            var params = `foldername=${e.FolderName}&description=${e.Description}&folder_Id=${vm.folderId}`;
          }
          console.log('params', params);
          CustomDataService.AddNewCustomFolder(params).then(response => {
            console.log('response', response);
            if (response.data[0].Value == 2) {
              vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Folder")]), 'success', true, vm.$t('Alert'));
              vm.close();
              vm.$parent.GetViewList();
            }
            else if (response.data[0].Value == 1) {
              vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Folder")]), 'success', true, vm.$t('Alert'));
              vm.close();
              vm.$parent.GetViewList();
            }
            else {
              vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("FolderName")]), 'danger', true, vm.$t('Alert'));
              vm.close();
              vm.$parent.GetViewList();
            }
          }).catch(err => {
            console.log(err)
          });
               
        },
  }
}
</script>
