<template>
    <div id="ShowSubModuleFields" role="dialog" class="modal my-popups right fade" v-cloak>
        <div role="document" class="modal-dialog" style="top:0px!important; height: 100%;">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" v-on:click="close()">
                        <em aria-hidden="true" class="fa fa-chevron-left"></em>
                    </button>
                    <h4 id="myModalLabel" class="modal-title">Sub Module Fields</h4>
                    <!-- <span class="user-guide">
                        <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button"
                            aria-expanded="false" aria-controls="collapseExample">User
                            Guide</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('User_Guide_Add_Custom_Report')"></div>
                            </div>
                        </div>
                    </span> -->
                </div>
                <div class="modal-body">
                    <loader :isVisible="isLoading"></loader>

                    <div class="search-box">
                        <input type="text" id="fieldSearch" class="form-control cursor-pointer" v-model="searchQuery"
                            placeholder="Search fields..." />
                    </div>

                    <div id="modalContent">
                        <div class="card mt-3" v-for="(item, index) in filteredFields" :key="item.SubModule">

                            <div class="card-header">
                                <h3 class="section-title">
                                    {{ GetLocalizedValue(item.SubModule) }}
                                </h3>
                            </div>
                            <div class="card-body">
                                <ul class="fields-list">
                                    <li v-for="Obj in item.CustomFields" :key="Obj.DisplayName">
                                        {{ GetLocalizedValue(Obj.DisplayName) }}
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>

                    <div v-if="filteredFields.length === 0 && searchQuery.trim() !== ''" class="text-center text-danger no-record border p-2 mt-2">
                        No records found
                    </div>

                </div>
            </div>
        </div>
    </div>

</template>

<script>
import DataService from "@/services/DataService";
export default {
    props: {
        ModuleName: {
            type: String,
            Required: true
        },
        SubModuleCode: {
            type: String,
            Required: true
        }
    },
    watch: {
        ModuleName(newVal, oldVal) {
            if (newVal && this.SubModuleCode) {
                this.GetSubModuleFieldList();
            }
        },
        SubModuleCode(newVal, oldVal) {
            if (newVal && this.ModuleName) {
                this.GetSubModuleFieldList();
            }
        }
    },
    computed: {
        filteredFields() {
            
            if (!this.searchQuery) return this.SubmoduleFields;

            const query = this.searchQuery.toLowerCase();

            // Filter each submodule's CustomFields
            return this.SubmoduleFields
                .map(sub => {
                    const filteredFields = sub.CustomFields.filter(field =>
                        field.DisplayName.toLowerCase().includes(query)
                    );
                    return {
                        ...sub,
                        CustomFields: filteredFields
                    };
                })
                .filter(sub => sub.CustomFields.length > 0); // Remove submodules with no matching fields
        }
    },
    data() {
        return {
            isLoading: false,
            SubmoduleFields: [],
            searchQuery: ""
        }
    },
    created: function () {
        
        var vm = this;
        vm.GetSubModuleFieldList();
    },

    methods: {

        close: function () {
            
            var vm = this;
            vm.searchQuery = '';
            $("#ShowSubModuleFields").modal("hide");
            $("body").removeClass("modal-open");
            $(".modal-backdrop").remove();

        },
        GetSubModuleFieldList: async function () {
            
            var vm = this;
            if (vm.ModuleName != undefined && vm.SubModuleCode != undefined) {
                var params = `moduleName=${vm.ModuleName}&subModuleCode=${vm.SubModuleCode}&viewId&reqFrom=Reporting`;
                await DataService.GetListFields(params).then(response => {
                    
                    vm.SubmoduleFields = response.data.AVAILABLE_FIELDS[0];

                }).catch(err => { console.log(err); })

            }
        },

    }
}
</script>

