<template>
    <div id="CustommodalManageView" role="dialog" class="modal my-popups right fade" v-cloak>
        <div role="document" class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" v-on:click="close()">
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>
                    <h4 id="myModalLabel" class="modal-title">{{ (ReportID == 0) ? $t("AddCustomReport") :
                        $t("EditCustomReport") }}</h4>
                    <span class="user-guide">
                        <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button"
                            aria-expanded="false" aria-controls="collapseExample">User
                            Guide</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('User_Guide_Add_Custom_Report')"></div>
                            </div>
                        </div>
                    </span>
                </div>
                <div class="modal-body">
                    <loader :isVisible="isLoading"></loader>
                    <div class="w-100 float-left leftsideicon" v-if="ShowLisiting">
                        <dynamic-form v-if="DataLoaded" lang="en" ref="modalAddCustomForm" :buttons="buttons"
                            :schema="FormSchema" v-on:OnSubmit="SaveDynamicreport">
                            <template v-slot:tgslot-modulelist="{ data }">
                                <div :class="{ 'container-fluid p-0 new_accordians_des des_acr': true, 'disabled': (ReportID != 0) }">
                                    <div class="accordion md-accordion left-filter" id="accordionEx">
                                        <div class="border p-3  bg-light">
                                            <div class="card" v-for="(parentItem, parentIndex) in MenuDataGroupBy"
                                                :key="parentItem.GroupId">
                                                <div class="card-header heading-border" role="tab"
                                                    :id="'MainColumn' + parentIndex">
                                                    <h5 class="mb-0"><a data-toggle="collapse"
                                                            data-parent="#accordionEx"
                                                            :data-target="'#ColumnCollapse' + parentIndex"
                                                            href="javascript:;" aria-expanded="false"
                                                            :aria-controls="'ColumnCollapse' + parentIndex"
                                                            class="collapsed">
                                                            <span class="">
                                                                {{ parentItem.GroupName }}</span><em
                                                                class="fa fa-angle-down rotate-icon"
                                                                aria-hidden="true"></em></a>
                                                    </h5>
                                                </div>
                                                <div :id="'ColumnCollapse' + parentIndex"
                                                    class="collapse multi-collapse" role="tabpanel"
                                                    :aria-labelledby="'MainColumn' + parentIndex"
                                                    data-parent="#accordionEx">
                                                    <div class="user_type_choice mt-2 coloredicons">
                                                        <div class="btn-group btn-group-toggle custom-reports-icons my-2"
                                                            data-toggle="buttons"
                                                            v-for="(item, index) in parentItem.GroupData"
                                                            v-on:click="getids(item.subModuleId, parentItem.GroupId, parentItem.GroupName, item.subModuleCode)"
                                                            :key="item.subModuleId">
                                                            <label
                                                                v-bind:class="{ 'btn btn_cstm': true, 'active': (item.subModuleId == selectedModuleId) }"
                                                                v-bind:for="'cls_usertype' + index + parentIndex">
                                                                <input type="radio" v-bind:value="item.subModuleId"
                                                                    name="cls_usertype"
                                                                    v-bind:id="'cls_usertype' + index + parentIndex"
                                                                    v-model="selectedModuleId" />
                                                                <span class="d-block pb-1"><em
                                                                        class="fa fa-folder-open-o bg-transparent"
                                                                        aria-hidden="true"></em></span>{{
                                                                            $t(item.subModuleName)
                                                                        }}
                                                                       
                                                                <button class="view-button btn-info" @click.stop="handleClick(parentItem.GroupName, item.subModuleCode)">
                                                                    View Fields
                                                                </button>
                                                            </label>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <span class="invalid-feedback d-block" v-if="data.error != ''">{{ data.error }}</span>
                            </template>
                        </dynamic-form>
                        <div class="parentdiv datarepfix show_hide_folder" style="right:0px; top:-32px;"
                            v-if="isShowAddFolder">
                            <div class="verticle-action lft25">
                                <span id="action-list" class="list-actions text-left p-0 datarep mt-3"
                                    style="width:394px">
                                    <div class="float-left col-12 p-0">
                                        <div class="container-fluid p-0 mb-3">
                                            <div class="row">
                                                <div class="col-md-12">
                                                    <div class="theme-primary partition-full">
                                                        <span class="p-name text-white mt-2">{{ $t('AddNewFolder')
                                                        }}</span>
                                                        <em aria-hidden="true"
                                                            class="fa fa-times close close-action_cv close-folder-popup"
                                                            style="opacity:9" @click="CloseFolderPopup()"></em>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-12 float-left">
                                            <dynamic-form lang="en" :buttons="FolderButtons" :schema="FolderFormSchema"
                                                v-on:OnSubmit="SaveNewFolder"></dynamic-form>
                                        </div>
                                    </div>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
             <ViewSubmoduleFields v-if="ShowSubModuleFields" v-on:close="ShowSubModuleFields = false" :ModuleName="moduleName" :SubModuleCode="submodulecode"></ViewSubmoduleFields>
        </div>
    </div>
    
</template>

<script>
import DataService from "@/services/DataService";
import ViewSubmoduleFields from "../../Common/Modal/modalViewSubmoduleFields.vue"
export default {
    props: {
        ReportID: {
            type: [Number, String],
            Required: true
        }
    },
    components: {
       ViewSubmoduleFields
    },
    data() {
        return {
            isLoading: false,
            ShowLisiting: true,
            MenuDataGroupBy: [],
            moduleName: 'ALL',
            reqFrom: 'Reporting',
            foldernamelisting: [],
            selectedfoldername: null,
            modelreportname: '',
            submoduleid: null,
            moduleid: null,
            Version: 1.1,
            submodulecode: null,
            selectedModuleId: null,
            foldernamemodel: '',
            folderdescmodel: '',
            isShowAddFolder: false,
            updatedmodulename: '',
            updatedsubmodulecodename: '',
            updatedreportid: '',
            DataLoaded: 0,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('ReportName'),
                            name: "ReportName",
                            value: "",
                            placeholder: "Enter Report Name",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('FolderName'),
                            name: "FolderID",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.AddNewFolderPopup
                            },
                            validationRules: "required",
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Modules'),
                            name: "modulelist",
                            value: "",
                            placeholder: "",
                            //visibility:true,
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            validationRules: "required",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save and Next"
                }
            ],
            FolderFormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FolderName'),
                            name: "FolderName",
                            value: "",
                            placeholder: "Enter Folder Name",
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "Enter Descripttion",
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            validationRules: "required",
                        },
                    ]
                }
            ],
            FolderButtons: [
                {
                    type: "submit",
                    class: "btn btn-success save mb-2",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                }
            ],
            ShowSubModuleFields: false,
            
        }
    },
    created: function () {
        var vm = this;
        vm.GetFavDashBoardData();
        vm.folderlisting();
        vm.DataLoaded = 1;
    },
    updated: function () {
        var vm = this;
        if (vm.ReportID != '') {
            vm.EditDynamicreport(vm.ReportID);
        }
        vm.selectedfoldername = "";
        vm.selectedModuleId = null;
        vm.modelreportname = "";
    },
    mounted: function () {
        $('#CustommodalManageView').modal({
            backdrop: 'static',
            keyboard: false
        })
    },
    methods: {
        handleClick(ModuleName, SubModuleCode) {
            
            var vm = this;
            vm.ShowSubModuleFields = true;
            vm.moduleName = ModuleName;
            vm.submodulecode = SubModuleCode;
           
            $("#ShowSubModuleFields").modal('show');
            setTimeout(function () {
                $("#ShowSubModuleFields").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        close: function () {
            var vm = this;
            vm.selectedfoldername = "";
            vm.selectedModuleId = null;
            vm.modelreportname = "";
            vm.FormSchema[0].Data[0].value = '';
            vm.FormSchema[0].Data[1].value = '';
            vm.$refs.modalAddCustomForm.validateForm.reset();
            // var getelement = document.querySelectorAll('*');
            // getelement.forEach(Elements => {
            //     Elements.classList.remove('show');
            // })
            $("#CustommodalManageView").modal("hide");
            $("body").removeClass("modal-open");
            $(".modal-backdrop").remove();
            vm.CloseFolderPopup();
        },
        folderlisting: function () {
            var vm = this;
            DataService.DDLfolderListing()
                .then(response => {
                    if (response.data != null && response.data != "") {
                        let sortListing = [];
                        sortListing = response.data;
                        vm.foldernamelisting = sortListing.sort((FirstValue, SecondValue) => FirstValue.foldeR_NAME.localeCompare(SecondValue.foldeR_NAME));
                        vm.FormSchema[0].Data[1].config.options = []
                        vm.foldernamelisting.forEach((item, index) => {
                            vm.FormSchema[0].Data[1].config.options.push({
                                value: item.foldeR_ID,
                                name: item.foldeR_NAME
                            })
                        })
                        vm.$refs.modalAddCustomForm.UpdateKeyValue();
                        vm.noRecord = false;
                    }
                }).catch(err => {
                    console.log(err);
                })
        },
        async GetFavDashBoardData() {
            
            var vm = this;
            vm.isLoading = true;
            var params = `moduleName=${vm.moduleName}&reqFrom=${vm.reqFrom}`;
            DataService.GetAddReportDashboard(params)
                .then((response) => {
                    if (response.statusText = 'Ok') {
                        vm.MenuData = response.data.Data;
                        var MenuGroupData = [];
                        if (vm.MenuData != null && vm.MenuData.length > 0) {
                            var groupId = vm.MenuData[0].moduleId;
                            $.each(vm.MenuData, $.proxy(function (index, item) {
                                groupId = item.moduleId;
                                var chkGroup = $.grep(MenuGroupData, function (element, index) {
                                    return element.GroupId == groupId;
                                });
                                if (chkGroup.length == 0) {
                                    var returnedData = $.grep(vm.MenuData, function (element, index) {
                                        return element.moduleId == groupId;
                                    });
                                    if (returnedData.length > 0) {
                                        var objGroup = {
                                            GroupId: groupId,
                                            GroupName: returnedData[0].moduleName,
                                            GroupData: returnedData[0].subModules
                                        };
                                        MenuGroupData.push(objGroup);
                                    }
                                }
                            },));
                            vm.MenuDataGroupBy = MenuGroupData;

                        }
                    }
                    vm.isLoading = false;
                });
        },
        SaveDynamicreport: function (formfields) {
            var vm = this;
            var Reportid = '';
            // vm.updatedreportid = ReportID;
            var params = `Report_Id=${Reportid}&ReportName=${formfields.ReportName.trim()}&Folder_Id=${formfields.FolderID}&Module_Id=${vm.moduleid}&Sub_Module_Id=${vm.submoduleid}&Ref_Code=${vm.submodulecode}&Version=${vm.Version}`;
            if (vm.moduleid != null && vm.submoduleid != null && vm.submodulecode != null) {
                DataService.AddDynamicreport(params).then(response => {
                    if (response.data[0].Value == 0) {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Report name")]), 'danger', true, vm.$t('Alert'));

                    }
                    else if (response.data[0].Value == -1) {
                        vm.ShowAlert(vm.$t("Failure", [vm.$t("Report name")]), 'danger', true, vm.$t('Alert'));
                    }
                    else {
                        this.$router.push({
                            name: 'AddDataReport',
                            params: {
                                Modulename: vm.moduleName, Submodulecode: vm.submodulecode, ReportName: formfields.ReportName.trim(), ReportID: response.data[0].Value, NewReportInserted: 'Save',
                                Module_Id: vm.moduleid, Folder_Id: formfields.FolderID, Sub_Module_Id: vm.submoduleid, Version: vm.Version, Action: 'Add'
                            }
                        });
                        vm.close();
                    }
                }).catch()
            } else {
                vm.ShowAlert(formfields.ReportName == '' && formfields.FolderID == '' ? vm.$t("FillRequiredFields") : vm.$t("SelectSubModule"), 'danger', true, vm.$t('Alert'));
                vm.isLoading = false;
            }
        },
        EditDynamicreport: function (ReportID) {
            var vm = this;
            var params = `&Report_id=${ReportID}`;
            DataService.Editdyanmicreport(params).then(response => {
                vm.modelreportname = response.data.ReportName;
                vm.selectedfoldername = response.data.Folder_Id;
                vm.moduleid = response.data.Module_Id;
                vm.submoduleid = response.data.Sub_Module_Id;
                vm.submodulecode = response.data.Ref_Code;
                vm.selectedModuleId = response.data.Sub_Module_Id;
                vm.updatedmodulename = response.data.module_name;
                vm.updatedsubmodulecodename = response.data.Ref_Code;
                if (vm.updatedmodulename != undefined && vm.updatedmodulename != '') {
                    vm.SaveDynamicreport(vm.ReportID);
                }
            }).catch(err => { console.log(err); })
        },
        getids: function (subModuleId, GroupId, GroupName, subModuleCode) {
            
            var vm = this;
            vm.submoduleid = subModuleId;
            vm.moduleid = GroupId;
            vm.moduleName = GroupName;
            vm.submodulecode = subModuleCode;
        },
        AddNewFolderPopup: function () {
            var vm = this
            vm.isShowAddFolder = true;
        },
        CloseFolderPopup: function () {
            var vm = this
            vm.foldernamemodel = '';
            vm.folderdescmodel = '';
            vm.FolderFormSchema[0].Data[0].value = "";
            vm.FolderFormSchema[0].Data[1].value = "";
            vm.$refs.modalAddCustomForm.validateForm.reset();
            vm.isShowAddFolder = false;
        },
        SaveNewFolder: function (formfields, val) {
            var vm = this;
            var params = `foldername=${formfields.FolderName}&description=${formfields.Description}`;
            DataService.AddNewCustomFolder(params).then(response => {
                if (response.data[0].Value == 1) {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Folder")]), 'success', true, vm.$t('Alert'));
                    vm.folderlisting();
                    vm.CloseFolderPopup();
                }
                else if (response.data[0].Value == 0) {
                    vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Folder name")]), 'danger', true, vm.$t('Alert'));
                }
            })
        }
    }
}
</script>


